.dialogs__header {
  padding: 10px;
  margin: 0;
  margin-bottom: 20px;
  
  grid-column: 1/-1;
  text-align: center;
  border-bottom: 1px solid rgba(0, 0, 0, 0.458);
}

.dialogs__innerWrapper {
  margin-bottom: 20px;

  display: grid;
  grid-template-columns: 3fr 10fr;
  gap: 30px;
}

.dialogs__contactsList {
  padding: 0;
  
  gap: 10px;
}

.dialogs__messagesList {
  padding: 10px;
  
  gap: 20px;
}

.dialogs__contactsList,
.dialogs__messagesList {
  margin: 0;

  max-height: 73vh;
  
  display: flex;
  flex-direction: column;
  
  list-style: none;
  overflow-y: auto;
  overflow-x: hidden;

  &::-webkit-scrollbar {
    width: 3px;
    background-color: rgba(127, 155, 255, 0.153);
  }

  &::-webkit-scrollbar-thumb {
    width: 3px;
    background-color: rgba(97, 73, 182, 0.53);
  }
}

.dialogs__addBlock {
  margin-top: auto;
  
  position: relative;
}

.dialogs__addBlock textarea {
  padding: 20px;

  width: 100%;
  
  background-color: rgba(240, 248, 255, 0.508);
  outline: none;
  resize: none;
}

.dialogs__addBlock button {
  margin: 0;
  padding: 0;
  
  width: 40px;
  height: 40px;
  
  position: absolute;
  right: calc(40px / 2);
  top: calc(50% - 40px / 2);
  
  font-size: 20px;
  
  background-color: rgba(156, 207, 251, 0.25);
  border: none;
  border-radius: 50%;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.25);

  cursor: pointer;
  transition: all 0.2s ease;

    &:hover {
    background-color: rgba(156, 207, 251, 0.5);
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.5);
  }
}
