.dialogs__message {
  padding: 20px;
  
  width: fit-content;
  
  position: relative;
  
  background-color: rgba(215, 222, 250, 0.5);
  box-shadow: 0px 6px 15px -10px #00000080;
  border-radius: 10px;
  border-bottom-left-radius: 0;

  &::after {
    width: 10px;
    height: 10px;
    
    position: absolute;
    content: '';
    bottom: 0;
    left: -10px;
    
    background-color: rgba(215, 222, 250, 0.5);
    border-top-left-radius: 100%;
  }
}
